import { useState, useEffect } from "react";
import { useNavigate } from "react-router-dom";
import "../styles/HomePage.css";
import AuthModal from "../components/AuthModal";

export default function HomePage() {
  const [showRules, setShowRules] = useState(false);
  const [showAuth, setShowAuth] = useState(false);
  const [user, setUser] = useState(null);
  const navigate = useNavigate();

  useEffect(() => {
    const storedUser = localStorage.getItem('lingo_user');
    if (storedUser) setUser(storedUser);
  }, []);

  const handleLogin = (username) => {
    localStorage.setItem('lingo_user', username);
    setUser(username);
    setShowAuth(false);
  };

  const startGame = () => {
    navigate("/game");
  };

  return (
    <div className="home-page" data-testid="home-page">
      <div className="lingo-background">
        <div className="lingo-circles"></div>
      </div>
      
      <div className="home-content">
        {/* st Bar: Kullanc Bilgisi */}
        <div className="absolute top-4 right-4 z-20">
          {user ? (
            <div className="flex items-center gap-3 bg-black/30 p-2 pr-4 rounded-full border border-white/10 backdrop-blur-md">
              <div className="w-8 h-8 bg-yellow-500 rounded-full flex items-center justify-center text-black font-bold">
                {user[0].toUpperCase()}
              </div>
              <span className="text-white font-medium">{user}</span>
              <button onClick={() => { localStorage.removeItem('lingo_user'); setUser(null); }} className="text-xs text-red-400 hover:text-red-300 ml-2">k</button>
            </div>
          ) : (
            <button onClick={() => setShowAuth(true)} className="px-4 py-2 bg-white/10 hover:bg-white/20 text-white rounded-full text-sm font-bold transition-all border border-white/10">
               GR YAP
            </button>
          )}
        </div>

        <div className="logo-section">
          <h1 className="lingo-title" data-testid="game-title">
            <span className="title-lingo">LINGO</span>
            <span className="title-turkiye">TRKYE</span>
          </h1>
          <p className="subtitle">Kelime Tahmin Oyunu</p>
        </div>

        <div className="menu-buttons">
          <button 
            className="menu-btn start-btn" 
            onClick={startGame}
            data-testid="start-game-btn"
          >
            <span className="btn-icon"></span>
            OYUNA BALA
          </button>
          
          <button 
            className="menu-btn" 
            onClick={() => navigate("/leaderboard")}
            style={{ background: 'rgba(255, 255, 255, 0.1)', color: '#fbbf24', border: '1px solid rgba(251, 191, 36, 0.3)' }}
          >
            <span className="btn-icon"></span>
            LDER TABLOSU
          </button>

          <button 
            className="menu-btn rules-btn" 
            onClick={() => setShowRules(!showRules)}
            data-testid="rules-btn"
          >
            <span className="btn-icon"></span>
            OYUN KURALLARI
          </button>
        </div>

        {showRules && (
          <div className="rules-modal" data-testid="rules-modal">
            <div className="rules-content">
              <button 
                className="close-btn" 
                onClick={() => setShowRules(false)}
                data-testid="close-rules-btn"
              >
                
              </button>
              
              <h2> Nasl Oynanr?</h2>
              
              <div className="rules-section">
                <h3> Oyun Ak</h3>
                <ul>
                  <li><strong>TUR 1:</strong> 3 adet 4 harfli + 3 adet 5 harfli kelime</li>
                  <li><strong>TUR 2:</strong> 3 adet 5 harfli + 3 adet 6 harfli kelime</li>
                </ul>
              </div>

              <div className="rules-section">
                <h3> Sre ve Deneme</h3>
                <ul>
                  <li>Her kelime iin <strong>5 deneme hakknz</strong> var</li>
                  <li>Her tahmin iin <strong>15 saniye</strong> sreniz var</li>
                  <li>Sre biterse o soru <strong>YANAR</strong> ve puan alamazsnz</li>
                  <li>Geersiz kelime girerseniz o soru <strong>YANAR</strong></li>
                </ul>
              </div>

              <div className="rules-section">
                <h3> Renk Sistemi</h3>
                <div className="color-examples">
                  <div className="color-box green">Yeil - Doru yerde</div>
                  <div className="color-box yellow">Sar - Var ama yanl yerde</div>
                  <div className="color-box gray">Gri - Kelimede yok</div>
                </div>
              </div>

              <div className="rules-section">
                <h3> Puanlama</h3>
                <ul>
                  <li><strong>4 Harfliler:</strong> 1000 Puan (Yanl: -100)</li>
                  <li><strong>5-6 Harfliler:</strong> 2000 Puan (Yanl: -200)</li>
                </ul>
              </div>

              <div className="rules-section">
                <h3> Final Turu (Deathmatch)</h3>
                <ul>
                  <li>Toplam <strong>120 saniye</strong> sreniz var (Sre durmaz!)</li>
                  <li>Srasyla 4, 5, 6 ve 7 harfli kelimeler sorulur</li>
                  <li>Her kelimeyi bildiinizde dlnz katlanr</li>
                  <li>Bilemezseniz ayn seviyeden yeni kelime gelir</li>
                </ul>
              </div>

              <p className="good-luck"> Bol ans!</p>
            </div>
          </div>
        )}

        {showAuth && (
          <AuthModal onClose={() => setShowAuth(false)} onLogin={handleLogin} />
        )}

        <a href="https://muhammedcihann.github.io/portfolio/" target="_blank" className="mc-badge">
            <span>Developed by</span>
            <strong>MC</strong>
        </a>
      </div>
    </div>
  );
}